<template>
  <BaseLayout>
    <Breadcrumbs />
    <section class="static pt-7">
      <BContainer>
        <h1>Blog</h1>
        <BRow style="width: 100%; justify-content: end">
          <BPagination
            style="
              margin-left: auto;
              margin-right: 0;
              justify-content: flex-end !important;
            "
            v-model="currentPageNr"
            :total-rows="totalBlogs"
            :per-page="blogsPerPage"
          ></BPagination>
        </BRow>
        <BRow>
          <BCol
            v-for="(item, index) in currentPage"
            :key="index"
            lg="4"
            class="pb-30 pb-lg-25"
          >
            <div style="width: 100%; height: 200px">
              <img
                style="width: 100%; max-height: 200px"
                :src="item.featured_image"
              />
            </div>
            <h3 style="padding-top: 10px; padding-bottom: 10px">
              {{ item.title }}
            </h3>
            <div v-html="item.short_filtered_content"></div>
            <BButton
              @click="$router.push('/blog/' + item.identifier)"
              type="button"
              variant="info"
              class="text-white form-submit-btn"
              >{{ $t("read_more") }}</BButton
            >
          </BCol>
        </BRow>
      </BContainer>
    </section>
  </BaseLayout>
</template>
<script setup>
import { computed, onMounted } from "vue";
import { useStore } from "vuex";
import BaseLayout from "@/base/core/components/common/BaseLayout";
import Breadcrumbs from "@/base/core/components/Breadcrumbs";

const store = useStore();

onMounted(() => {
  const bcrumb = {
    current: "Blog",
    routes: [],
  };
  store.commit("breadcrumbs/set", bcrumb);
});

const currentPageNr = computed({
  get: () => store.getters["blog/getCurrentPageNr"],
  set: (val) => store.dispatch("blog/goToPage", { pageNr: val }),
});

const totalBlogs = computed(() => store.getters["blog/getTotalBlogs"]);
const blogsPerPage = computed(() => store.getters["blog/getBlogsPerPage"]);
const currentPage = computed(() => store.getters["blog/getCurrentPage"]);
</script>
